const config = Symbol('config')
const isCompleteURL = Symbol('isCompleteURL')
const requestBefore = Symbol('requestBefore')
const requestAfter = Symbol('requestAfter')
import con from './config/config'
import md5 from '@/common/md5.js'

class MinRequest {
	[config] = {
		baseURL: '',
		header: {
			'content-type': 'application/json'
		},
		method: 'GET',
		dataType: 'json',
		responseType: 'text'
	}

	interceptors = {
		request: (func) => {
			if (func) {
				MinRequest[requestBefore] = func
			} else {
				MinRequest[requestBefore] = (request) => request
			}

		},
		response: (func) => {
			if (func) {
				MinRequest[requestAfter] = func
			} else {
				MinRequest[requestAfter] = (response) => response
			}
		}
	}

	static[requestBefore](config) {
		return config
	}

	static[requestAfter](response) {
		return response
	}

	static[isCompleteURL](url) {
		return /(http|https):\/\/([\w.]+\/?)\S*/.test(url)
	}

	setConfig(func) {
		this[config] = func(this[config])
	}

	request(options = {}) {

		var comInfo = uni.getStorageSync('comInfo');
		options.data.requestdate = Date.parse(new Date());
		let arr = [];
		for (let i in options.data) {
			var value = options.data[i];
			if (value == null || value == undefined) {
				value = '';
				options.data[i] = '';
			}
			arr.push(i.toUpperCase() + "=" + encodeURIComponent(value) + "&");
		}
		var signStr = (arr.sort().join('') + "KEY=" + con.Sign.Key).toUpperCase();
		options.data.Sign = md5(signStr);
		//签名处理结束

		var user = uni.getStorageSync('uerInfo');
		if (user != '') { 
			options.data.token = user.token;
		}
		options.data.token = 'db93b2b605c40af39d6e75c9546efe757e2617c6';
		options.baseURL = options.baseURL || this[config].baseURL
		options.dataType = options.dataType || this[config].dataType
		options.url = MinRequest[isCompleteURL](options.url) ? options.url : (options.baseURL + options.url)
		options.data = options.data
		options.header = options.header || this[config].header
		options.method = options.method || this[config].method

		options = {
			...options,
			...MinRequest[requestBefore](options)
		}


		return new Promise((resolve, reject) => {
			options.success = function(res) {
				if (res.data.status == -2) {
					uni.showModal({
						title: '账号已封禁',
						content: '您的账号已封禁，无权访问此页面',
						showCancel: false,
						success: (res) => {
							if (res.confirm) {
								uni.switchTab({
									url: '/pages/index/index'
								});
							}
						}
					});
					return;
				} else if (res.data.status == -1 && user != '') { //登录超时

					var appId = con.H5Wei.AppId;
					var redirect_uri = encodeURIComponent(con.SITE +
						'#/pages/member/login?backUrl='); //返回网址
					var url =
						'https://open.weixin.qq.com/connect/oauth2/authorize?appid=' + appId +
						'&redirect_uri=' + redirect_uri +
						'&response_type=code&scope=snsapi_userinfo&state=1#wechat_redirect';
					window.location.href = url;
					return;
				}
				resolve(MinRequest[requestAfter](res))
			}
			options.fail = function(err) {
				reject(MinRequest[requestAfter](err))
			}
			uni.request(options)
			//console.log('abc')
		})
	}

	get(url, data, options = {}) {
		options.url = url
		options.data = data
		options.method = 'GET'
		return this.request(options)
	}

	post(url, data, options = {}) {
		options.url = url
		options.data = data
		options.method = 'POST'
		options.header = {
			'content-type': 'application/x-www-form-urlencoded'
		};
		return this.request(options)
	}
}

MinRequest.install = function(Vue) {
	Vue.mixin({
		beforeCreate: function() {
			if (this.$options.minRequest) {
				Vue._minRequest = this.$options.minRequest
			}
		}
	})
	Object.defineProperty(Vue.prototype, '$request', {
		get: function() {
			return Vue._minRequest.apis
		}
	})
}

export default MinRequest
